/* Container */

.wrapper {
  width: 100%;
  max-width: 100%;
  margin: auto;
  overflow: hidden;
}

.carousel {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 56.25%;
  background: #ddd;
}

/* Images */

.carousel-img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  -webkit-transition: opacity ease-out 0.5s;
  transition: opacity ease-out 0.5s;
}

.carousel-img-displayed {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  z-index: 2;
  width: 100%;
}
.carousel-img-displayed > img {
  object-fit: contain;
  width: 100%;
}
.carousel-img-displayed > span {
  position: absolute;
  color: #0e467d;
  background-color: #ddd;
  padding: 20px;
  border-radius: 7px;
  font-size: 35px;
  margin-bottom: 20%;
  font-weight: 800;
}

.carousel-img-hidden {
  display: block;
  opacity: 0;
  z-index: 1;
}

/* Flèches de défilement */

.carousel-arrow {
  z-index: 3;
  display: block;
  position: absolute;
  width: 36px;
  height: 36px;
  top: 50%;
  margin-top: -18px;
  border-radius: 50%;
  border: 0;
  background-color: #fff;
  background-image: url("http://res.cloudinary.com/dnqehhgmu/image/upload/v1509720334/blue-arrow_jk1ydw.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 16px;
  cursor: pointer;
  -webkit-transition: background-size 0.15s ease-out;
  transition: background-size 0.15s ease-out;
}

.carousel-arrow:hover,
.carousel-arrow:focus {
  background-size: 22px 22px;
}

.carousel-arrow-next {
  right: 20px;
}

.carousel-arrow-prev {
  left: 20px;
  -webkit-transform: rotateZ(180deg);
  -ms-transform: rotate(180deg);
  transform: rotateZ(180deg);
}

@media only screen and (max-width: 1200px) {
  .carousel-img-displayed > span {
    padding: 20px;
    border-radius: 7px;
    font-size: 25px;
    margin-bottom: 20%;
    font-weight: 800;
  }
}
@media only screen and (max-width: 900px) {
  .carousel-img-displayed > span {
    padding: 20px;
    border-radius: 7px;
    font-size: 20px;
    margin-bottom: 30%;
    font-weight: 800;
  }
}
@media only screen and (max-width: 700px) {
  .carousel-img-displayed > span {
    padding: 5px;
    border-radius: 7px;
    font-size: 15px;
    margin-bottom: 30%;
    font-weight: 800;
  }
}
